/* divider */
.image-divider {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50%;
  width: 80%;
  overflow: hidden;
}

/* 
card size */
.card-size {
  height: 500px;
  width: 350px;
}

/* Contact */
icon-text {
  padding: 1rem;
  display: flex;
  justify-content: initial;
  align-items: center;
}

/* Glass */
.glass {
  position: relative;
  /* display: flex; */
  width: 100%;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 5px 8px 0 rgba(56, 56, 56, 0.3), 0 7px 20px 0 rgba(71, 70, 70, 0.3);
  padding: 1rem;
  backdrop-filter: blur(4.5px);
  -webkit-backdrop-filter: blur(4.5px);
  border-radius: 10px;
}
.row {
  --bs-gutter-x: 0rem !important;
}

@media (max-width: 991px) {
  /* .navbar{
    padding: 0;
  } */
  .navbar-brand {
    font-size: 1.6rem !important;
  }
  .navbar-toggler {
    background-color: #484848;
  }
  .logo {
    margin-top: 0;
  }

  .menu_active {
    font-weight: bold;
    border-bottom: none;
  }
  .nav-item {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .navbar a:hover {
    color: aqua !important;
    background-color: rgb(56, 56, 88);
    border-radius: 5px;
    padding: 5px;
    box-shadow: inset;
  }
  .navbar-toggler-icon {
    display: flex;
  }
}
@media (max-width: 576px) {
  .navbar-expand-sm {
    flex-wrap: nowrap;
    justify-content: flex-start;
    background-color: #484848;
    
  }
  .navbar {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding-top: 0rem;
    padding-bottom: 0.5rem;
    background-color: #484848;
  }
  *,
  ::after,
  ::before {
    box-sizing: border-box;
  }
  
}

.form-control {
  display: block;
  width: 100%;
  padding: 0.375rem 0 ;
  font-size: 1.2re;
  font-weight: 400;
  line-height: 1.5;
  /* color: #212529; */
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #6aa0c7a3;
  -webkit-appearance: none;
  appearance: none;
  border-radius: 0.25rem;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}


.form-container {
  height: 580px;
}

.form-content-left {
  background: linear-gradient(
    90deg,
    rgb(39, 176, 255) 0%,
    rgb(0, 232, 236) 100%
  );
  border-radius: 10px 0 0 10px;
  position: relative;
}

.form-img {
  width: 80%;
  height: 80%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.form-img-2 {
  width: 60%;
  height: 60%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.form-success {
  text-align: center;
  font-size: 24px;
  margin-top: 80px;
  color: #fff;
}

.form-content-right {
  border-radius: 0 10px 10px 0;
  position: relative;
  background: linear-gradient(90deg, rgb(40, 40, 40) 0%, rgb(17, 17, 17) 100%);
}

.form {
  position: relative;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form h1 {
  font-size: 1rem;
  text-align: start;
  width: 80%;
  margin-bottom: 1rem;
  color: #fff;
}

.form-inputs {
  margin-bottom: 0.5rem;
  width: 80%;
}

.form-inputs p {
  font-size: 0.8rem;
  margin-top: 0.5rem;
  color: #f00e0e;
}

.form-label {
  display: inline-block;
  font-size: 1rem;
  margin-bottom: 6px;
  color: rgb(0, 0, 0);
}

.form-input {
  display: block;
  padding-left: 10px;
  outline: none;
  border-radius: 2px;
  height: 40px;
  width: 100%;
  border: none;
}

.form-input::-webkit-input-placeholder {
  color: #595959;
  font-size: 12px;
}

.form-input:-ms-input-placeholder {
  color: #595959;
  font-size: 12px;
}

.form-input::placeholder {
  color: #595959;
  font-size: 12px;
}

.form-input-btn {
  width: 80%;
  height: 50px;
  margin-top: 10px;
  border-radius: 2px;
  background: linear-gradient(
    90deg,
    rgb(39, 176, 255) 0%,
    rgb(0, 232, 236) 100%
  );
  outline: none;
  border: none;
  color: #fff;
  font-size: 1rem;
}

.form-input-btn:hover {
  cursor: pointer;
  background: linear-gradient(
    90deg,
    rgb(39, 143, 255) 0%,
    rgb(12, 99, 250) 100%
  );
  transition: all 0.4s ease-out;
}

.form-input-login {
  font-size: 0.8rem;
  margin-top: 10px;
  color: #fff;
  width: 80%;
  text-align: center;
}

.form-input-login a {
  text-decoration: none;
  color: #27cdff;
  font-weight: 600;
}


